export type TUser = {
  did: string;
  name: string;
  certificationName: string;
  certificatedAte: string;
  expiredAt: string;
  issuerName: string;
  transactionHash: string;
  imageChecksum: string;
};

type TProps = {
  user: TUser;
}

export default function Profile({ user }: TProps) {
  return (
    <div>
      <div>
        <span class="name">{user.name}</span>
      </div>
    </div>
  );
}
